/*
  1) CONFIG - DEFAULT STATE:
*/
const initialState = {
  get_domains: false,
  input_spellchecked: "",
  input_first_word: "",
  input_str: "",
  input_tld: "",
  input_captcha: "",
  suggestions_options: {
    length_vs_relevance: 5,
    ext_suggestions_num: 12,
    group_by: "name",
    use_domain_hacks: true,
    use_word_hacks: true,
    use_synonyms: true,
    use_before_after: true
  }, // dump {key:value} options into here - will be passed to search API
  in_progress: false,
  params_stringified: "",
  search_now: 0
}

/*
  2) REDUCE STATE:
*/
const ThisReducer = (state = initialState, action) => {
  let newState = { ...state }

  /*
    3) HANDLE ACTIONS:
  */
  switch (action.type) {
    /*
     * trigger search when user interacts with the Search.js component
     */
    case "RX__search_now":
      newState.search_now++
      console.warn("search_now " + newState.search_now)
      break

    /*
     * inputs must all be set at the same time, to avoid multiple re-renders:
     */
    case "RX__set_inputs":
      {
        for (let key in action.data) {
          let value = action.data[key] || ""
          /*
           * Step 1: side effects and/or modify value
           */
          switch (key) {
            case "str":
              {
                // reset generated values
                newState.input_spellchecked = ""
                newState.input_first_word = ""
                // filter
                value = value.replace(/[^\w\d ]+/g, "")
                // set new first word
                let i_space = newState.input_str.indexOf(" ")
                if (i_space !== -1) {
                  newState.input_first_word = value.substring(0, i_space)
                } else {
                  newState.input_first_word = value
                }
                // URL
                let url_obj = object_from_querystring(window.location.search)
                url_obj.str = value
                set_window_title_location(url_obj)
              }
              break

            case "tld":
              {
                // URL
                let url_obj = object_from_querystring(window.location.search)
                url_obj.tld = value
                set_window_title_location(url_obj)
              }
              break

            case "spellchecked":
              {
                value = value.replace(/[^\w\d ]+/g, "")
              }
              break
          }
          /*
           * Step 2: set value
           */
          newState["input_" + key] = value || initialState["input_" + key]
          /*
           * Step 3: loose ends
           */
        }
      }
      break

    case "RX__clear_inputs":
      {
        // clean URL
        console.log("RX__clear_inputs", { str: "", tld: "" })
        set_window_title_location({})
        // reset state
        newState.input_tld = initialState.input_tld
        newState.input_str = initialState.input_str
        newState.input_captcha = initialState.input_captcha
        newState.input_spellchecked = initialState.input_spellchecked
        newState.input_first_word = initialState.input_first_word
      }
      break

    case "RX__set_suggestions_options":
      if (action.data) {
        for (let key in action.data) {
          newState.suggestions_options[key] = action.data[key]
        }
      } else {
        newState.suggestions_options = initialState.suggestions_options
      }
      break

    case "RX__in_progress":
      newState.in_progress = !!action.data
      break

    case "RX__reset":
      newState = { ...initialState }
      break

    case "RX__params_stringified":
      newState.params_stringified = action.data
      break
  }
  /*
    4) RETURN (COPY OF) STATE:
  */
  // console.log(
  //   [action.type, action],
  //   JSON.parse(JSON.stringify(newState)),
  // )
  return newState
}

export default ThisReducer
